import { useEffect, useState } from "react";

import React from "react";
import { dbService } from "fBase";
import Oweet from "components/Oweet";
import OweetFactory from "components/OweetFactory";

const Home = ({ userObj }) => {
  const [oweets, setOweets] = useState([]);

  useEffect(() => {
    //3.3 realtime 5:47
    dbService
      .collection("oweets")
      .orderBy("createdAt", "desc")
      .onSnapshot((Snapshot) => {
        const oweetArray = Snapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));
        setOweets(oweetArray);
      });
    return () => setOweets(null);
  }, []);

  return (
    <div className="container">
      <OweetFactory userObj={userObj} />
      <div style={{ marginTop: 30 }}>
        {oweets.map((feeds) => (
          <Oweet
            key={feeds.id}
            oweetObj={feeds}
            isOwner={feeds.creatorId === userObj.uid}
          />
        ))}
      </div>
    </div>
  );
};
export default Home;
