import Oweet from "components/Oweet";
import { authService, dbService } from "fBase";
import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";

const Profile = ({ userObj, refreshUser }) => {
  const [oweets, setOweets] = useState([]);
  const [newDisplayName, setNewDisplayName] = useState(userObj.displayName);
  const history = useHistory();
  const onLogOutClick = () => {
    authService.signOut();
    history.push("/");
  };
  const onChange = (event) => {
    const {
      target: { value },
    } = event;
    setNewDisplayName(value);
  };
  const onSubmit = async (event) => {
    event.preventDefault();
    // 밑에 updateProfile 에 프로필 사진도 넣어보자
    if (userObj.displayName !== newDisplayName) {
      await userObj.updateProfile({
        displayName: newDisplayName,
      });
      refreshUser();
    }
  };

  useEffect(() => {
    //내가한거
    dbService
      .collection("oweets")
      .where("creatorId", "==", userObj.uid)
      .orderBy("createdAt", "desc")
      .onSnapshot((Snapshot) => {
        const oweetArray = Snapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));
        setOweets(oweetArray);
      });
    return () => setOweets(null);
  }, []);

  return (
    <div className="container">
      <form onSubmit={onSubmit} className="profileForm">
        <input
          onChange={onChange}
          type="text"
          autoFocus
          placeholder="Display name"
          value={newDisplayName}
          className="formInput"
        />
        <input
          type="submit"
          value="Update Profile"
          className="formBtn"
          style={{
            marginTop: 10,
          }}
        />
      </form>
      <span className="formBtn cancelBtn logOut" onClick={onLogOutClick}>
        Log Out
      </span>
      {oweets.map((feeds) => (
        <Oweet
          key={feeds.id}
          oweetObj={feeds}
          isOwner={feeds.creatorId === userObj.uid}
        />
      ))}
    </div>
  );
};
export default Profile;
